.items {
  margin-top: 1rem;
  display: flex;
  width: 100vw;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  color: white;
  font-family: charm;

  .company {
    display: flex;
    flex-direction: column;

    .title {
      text-align: center;
      text-shadow: 0 0 0.1rem black;
    }

    .button {
      font-family: charm;
      cursor: pointer;
      color: white;
      font-size: 1rem;
      background: none;
      border: none;
      margin: 0.4rem;
      box-shadow: 0 0 0.1rem white;
      transition: scale 0.4s;

      &.facebook {
        box-shadow: none;
        font-size: 1.5rem;

        &:hover {
          filter: drop-shadow(0 0.1rem 0.1rem #224dda);
          scale: 1.2;
        }
      }

      &.instagram {
        font-size: 1.5rem;
        box-shadow: none;

        &:hover {
          filter: drop-shadow(0 0.1rem 0.1rem #c71ab9);
          scale: 1.2;
        }
      }

      &:hover {
        filter: drop-shadow(0 0.1rem 0.1rem #4b4a4a);
        scale: 1.1;
        color: #81817d;
      }
    }
  }

  .table {
    text-align: center;
  }
}

.copyright {
  color: white;
  text-align: center;
  font-size: 1rem;
  font-family: "Lobster";
}
