.services {
  margin-top: 12rem;
  padding: 2rem;

  @media only screen and (orientation: portrait) {
    margin-top: -1rem;
  }

  .intro {
    font-family: charm;
    font-size: 1.6rem;
    text-shadow: 0 0.1rem 0.1rem #b6b3b6;
    text-align: center;
    padding: 2rem;
    text-transform: uppercase;
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(90deg, #f1d9dd, #ebe5ea);
    padding: 1rem;
    filter: drop-shadow(0 0.1rem 0.1rem #755873);

    &.top {
      border-top-right-radius: 1rem;
      border-top-left-radius: 1rem;
    }

    &.bottom {
      border-bottom-right-radius: 1rem;
      border-bottom-left-radius: 1rem;
    }

    .title {
      padding: 1rem 2vw 0rem 0vw;
      border-right: solid 2rem #c0759e;
      border-top-right-radius: 50%;
      width: 40vw;
      text-decoration: underline;
      margin-left: 15vw;
      text-align: end;
      text-transform: uppercase;
      font-size: 1.5rem;
      font-family: "Merienda";

      @media only screen and (orientation: portrait) {
        width: 60vw;
        padding-right: 5vw;
      }
    }

    .text {
      line-height: 1.4rem;
      font-family: merienda;
    }
  }
}
