.salon {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 80vw;
  text-align: center;
  font-family: "charm";
  font-size: 1rem;

  @media only screen and (orientation: portrait) {
    width: 100vw;
  }

  &:nth-child(odd) {
    flex: 1;
  }

  &:nth-child(even) {
    flex: 2;
  }

  .text {
    padding: 1rem;
    width: 33vw;
    line-height: 1rem;

    @media only screen and (orientation: portrait) {
      width: 100vw;
    }
  }

  .photo {
    filter: drop-shadow(0 0.1rem 0.1rem #6e5c6b);
    width: 33vw;
    z-index: -1;

    @media only screen and (orientation: portrait) {
      width: 50vw;
    }
  }

  .owner {
    filter: drop-shadow(0 0.1rem 0.1rem #6e5c6b);
    position: absolute;
    right: -7vw;
    margin-top: 33%;
    width: 45vw;
    z-index: -1;

    @media only screen and (orientation: portrait) {
      position: relative;
      height: 30rem;
      width: auto;
      margin-top: 0;
    }
  }
}
