.arkana {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: -1rem 0 1rem black;
  font-family: charm;

  .brand {
    margin-top: 3rem;
    width: 60vw;
    border-radius: 0.5rem;
    filter: drop-shadow(0 0.1rem 0.1rem #6e5c6b);

    @media only screen and (orientation: portrait) {
      margin-bottom: 2rem;
    }
  }

  .container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    text-align: center;

    @media only screen and (orientation: portrait) {
      flex-direction: column;
    }

    .cosmetic {
      width: 20vw;
      border-radius: 1rem;
      filter: drop-shadow(0 0.1rem 0.1rem #8f8a8e);

      @media only screen and (orientation: portrait) {
        width: 40vw;
        margin-bottom: 1rem;
      }
    }

    .subcontainer {
      margin-bottom: 3rem;

      .title {
        text-decoration: underline;
      }
      
      .text {
        background: #e6d0d4;
        text-transform: uppercase;
        filter: drop-shadow(0 0rem 0.1rem #7e157e);
        padding: 0.5rem;
        border-radius: 0.4rem;

        @media only screen and (orientation: portrait) {
          margin-top: 3rem;
        }
      }
    }
  }
}
