.welcome {
  display: flex;
  flex-direction: row;
  background-color: #bbb5b6;
  width: 100vw;
  height: 20rem;
  font-family: "Charm", cursive;

  @media only screen and (orientation: portrait) {
    flex-direction: column;
    align-items: center;
    height: 100%;
  }

  .description {
    filter: drop-shadow(0 0.1rem 0.1rem #877288);
    background: linear-gradient(90deg, transparent, white);
    width: 100%;

    .text {
      font-size: 1rem;
      padding: 0rem 1vw 0rem 1vw;
      line-height: 1.15rem;
      max-width: 30rem;

      @media only screen and (orientation: portrait) {
        text-align: end;
        right: -10vw;
        position: relative;
      }
    }
  }

  .model {
    height: 20rem;

    @media only screen and (orientation: portrait) {
      width: 100vw;
      height: auto;
    }
  }

  .contact {
    left: 35vw;
    margin-top: 24rem;
    position: absolute;
    height: 10rem;
    width: 10rem;
    border: none;
    background: radial-gradient(circle, pink, white);
    border-radius: 50%;
    cursor: pointer;
    filter: drop-shadow(0 0.1rem 0.1rem #c0b3c0);
    transition: scale 0.5s;

    @media only screen and (orientation: portrait) {
      margin-top: 70%;
    }

    &:hover {
      scale: 1.05;
      filter: drop-shadow(0 0.1rem 0.1rem #f8f2f8);
    }

    .art {
      height: 10rem;
    }

    .text {
      font-size: 2rem;
      font-family: lobster;
    }
  }
}
